.login__container {
    max-width: 880px;
    height: 74px;
    margin: 0 auto 60px;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    box-sizing: border-box;
    border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 500px) {
    .login__container {
        padding: 0 30px;
        height: 56px;
        margin-bottom: 40px;
    }
}